import * as ActionTypes from './ActionTypes'

export const Comments = (state = {
    errorMessage: null,
    comments: []
  }, action) => {
  switch (action.type) {
    case ActionTypes.ADD_COMMENTS:
      return {...state, errorMessage: null, comments: action.payload};

    case ActionTypes.COMMENTS_FAILED:
      return {...state, errorMessage: action.payload};

    case ActionTypes.ADD_COMMENT:
      var comment = action.payload // we get the comments in the payload of action
      // comment.id = state.length // when we add a new comment we need a new id for it, which is equal to state.length
      // comment.date = new Date().toISOString() // when we add a newcomment we also need to display the date when comment was added
      //return state.concat(comment) // To prevent the mutation of current/previous state we use concat() method, this method returns a new state object by appending the comments to the state
      return {...state, comments:state.comments.concat(comment)}

    default:
      return state
  }
}
