import * as ActionTypes from './ActionTypes'
import { baseUrl } from '../shared/baseUrl';

//addComment is an action which is being created to add the new comment related to a particular dish to it's
// earlier set of comments. Whenweneed to add a new comment we need it's dishId, rating,author and comment
// hence we send these as parameters to the addComment and also these are the payload object item,because
//payload defines the data to be utilized from an action.
//Since this action is related to comments, it will act only on comment reducer
/** commenting this action as we are using the new action as shown below for displaying updated list of comments
export const addComment = (dishId, rating, author, comment) => ({
  type: ActionTypes.ADD_COMMENT,
  payload: {
    dishId: dishId,
    rating: rating,
    author: author,
    comment: comment
  }
})
*/

export const addComment = (comment) => ({
  type: ActionTypes.ADD_COMMENT,
  payload: comment
})

// To display the error message when comments loading fails
export const commentsFailed= (errorMessage) =>({
  type:ActionTypes.COMMENTS_FAILED,
  payload:errorMessage
})

//To add the comments
export const addComments =(comments) =>({
  type:ActionTypes.ADD_COMMENTS,
  payload:comments
})

//Thunk used to fetch comments
export const fetchComments =()=> (dispatch)=>{
  return fetch(baseUrl+"comments")
  .then(response=>{
    if(response.ok){
      return response;
    }
    else{
      var error = new Error('Error ' + response.status + ': ' + response.statusText)
      error.response=response
      throw error
    }
  },
  error=>{
    var errmess = new Error(error.message);
    throw errmess;
  })
  .then(response=>response.json())
  .then(comments=>dispatch(addComments(comments)))
  .catch(error => dispatch(commentsFailed(error.message)));
}

//Thunk used for posting new comment to the server
export const postComment =(dishId, rating, author, comment) => (dispatch) =>{
 const newComment ={
        dishId: dishId,
        rating: rating,
        author: author,
        comment: comment
 }
 newComment.date = new Date().toISOString();
 return fetch(baseUrl + 'comments',{
        method: "POST",
        body: JSON.stringify(newComment),
        headers: {
          "Content-Type": "application/json"
        },
        credentials: "same-origin"
 })
 .then(response => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error('Error ' + response.status + ': ' + response.statusText);
          error.response = response;
          throw error;
        }
      },
      error => {
            throw error;
      })
  .then(response=>response.json())
  .then(response => dispatch(addComment(response)))
  .catch(error =>  { console.log('post comments', error.message); alert('Your comment could not be posted\nError: '+error.message); });
}

//For loading the dishes
export const dishesLoading =()=>({
  type:ActionTypes.DISHES_LOADING
})

// To display the error message when loading fails
export const dishesFailed= (errorMessage) =>({
  type:ActionTypes.DISHES_FAILED,
  payload:errorMessage
})

//To add the dishes
export const addDishes =(dishes) =>({
  type:ActionTypes.ADD_DISHES,
  payload:dishes
})

//This is a thunk, which is used to fetch dishes
export const fetchDishes = () => dispatch => {
  // when fetchDishes is called the firstly dishes start loading and the after 2 seconds addDishes is called

  dispatch(dishesLoading(true)) 

  /**This set time out was used to add a delay while using data from .js file to replicate the scenario of server data fetch 
  Now since we are using json-server, lets comment this setTimeout function*/
    // setTimeout(() => {
    //   dispatch(addDishes(DISHES))
    // }, 2000)

  return fetch(baseUrl+ "dishes") // use the fetch() function to specify the Url
  .then(response=>{
    if(response.ok){
      return response;
    }
    else{
      var error = new Error('Error ' + response.status + ': ' + response.statusText)
      error.response=response
      throw error
    }
  },
  error=>{
    var errmess = new Error(error.message);
    throw errmess;
  })
  .then(response=> response.json()) // 
  .then(dishes => dispatch(addDishes(dishes)))
  .catch(error => dispatch(dishesFailed(error.message)));
}

