import React, { Component } from 'react'
import Menu from './MenuComponent'
import Home from "./HomeComponent"
import Header from "./HeaderComponent"
import Footer from "./FooterComponent"
import Contact from "./ContactComponent"
import DishDetail from "./DishDetailComponent"
import About from "./AboutComponent"
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {postComment , fetchDishes, fetchComments, fetchPromos, fetchLeaders, postFeedback} from '../redux/ActionCreators'
import { actions } from 'react-redux-form';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import "../../src/App.css"

//once we connect the mapStateToProps to the component with connect(), mapStateToProps gets state as an argument
const mapStateToProps = state => {
  return {
    dishes: state.dishes, // entire dishes state object is avaliable, to access individual properties (dishes.isLoading,dishes.errorMessage,dishes.dishes)
    comments: state.comments,
    promotions: state.promotions,
    leaders: state.leaders
  }
}

//once we connect mapDispatchToProps to component with connect(),mapDispatchToProps gets dispatch as an argument
const mapDispatchToProps = dispatch => ({
  postComment: (dishId, rating, author, comment) =>
    dispatch(postComment(dishId, rating, author, comment)) , 
  resetFeedbackForm: () => { dispatch(actions.reset('feedback'))},
  fetchDishes: () => dispatch(fetchDishes()),
  fetchComments:()=>dispatch(fetchComments()),
  fetchPromos:()=>dispatch(fetchPromos()),
  fetchLeaders:()=>dispatch(fetchLeaders()),
  postFeedback:(feedback) => dispatch(postFeedback(feedback))
})

class Main extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }

  // onDishSelect (dishId) {
  //   this.setState({ selectedDish: dishId })
  // }

  //This life cycle method is called immediately after component ismounted in DOM
  componentDidMount(){
    this.props.fetchDishes();
    this.props.fetchComments();
    this.props.fetchPromos();
    this.props.fetchLeaders();
  }

  render () {
      const HomePage = () => {
      return(
          <Home dish={this.props.dishes.dishes.filter((dish)=>dish.featured)[0]}
          dishesLoading={this.props.dishes.isLoading}
          dishesErrMess={this.props.dishes.errorMessage}
          promotion={this.props.promotions.promotions.filter((promo)=>promo.featured)[0]}
          promosLoading={this.props.promotions.isLoading}
          promosErrMess={this.props.promotions.errorMessage}
          leader={this.props.leaders.leaders.filter((leader) => leader.featured)[0]}
          leadersLoading={this.props.leaders.isLoading}
          leadersErrMess={this.props.leaders.errorMessage}
          />
      );
    }

    const DishWithId = ({match}) => {
      return(
          <DishDetail dish={this.props.dishes.dishes.filter((dish) => dish.id === parseInt(match.params.dishId,10))[0]}
            isLoading={this.props.dishes.isLoading}
            errMess={this.props.dishes.errorMessage} 
            comments={this.props.comments.comments.filter((comment) => comment.dishId === parseInt(match.params.dishId,10))} 
            commentsErrMess={this.props.comments.errMess}
            postComment={this.props.postComment} // add the props for new comments to DishDetail component for further handling
            />
      );
    };

    return (
      <div>
        <Header/>
        <TransitionGroup>
          <CSSTransition key={this.props.location.key} classNames="page" timeout={300}>
            <Switch location={this.props.location}>
            <Route path='/home' component={HomePage} />
            <Route exact path='/menu' component={() => <Menu dishes={this.props.dishes} />} />
            <Route path='/menu/:dishId' component={DishWithId} />
            {/**
            * <Route path='/menu/:dishId' component={DishWithId} /> -- here dishId is route parameter and in the DishWithId function we use the match
            * parameter from the router and access the id of selected dish (dishId)
            */}
            <Route exact path="/contactus" component={() =><Contact resetFeedbackForm={this.props.resetFeedbackForm} postFeedback={this.props.postFeedback}/>}/>
            <Route exact path="/aboutus" component={()=><About leaders={this.props.leaders}/>}/>
            <Redirect to="/home" />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
        <Footer/>
      </div>
    )
  }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Main));