import React from 'react';

export const Loading = () => {
    return(
        <div className="col-12">
            <span className="fa fa-spinner fa-pulse fa-3x fa-fw text-primary"></span>
            <p>Loading . . .</p>
        </div>
    );
};


/**
 * if a component is defined as default export component componentName, then while importing: import componentName from ""
 * if we only write export const componentName, then while importing: import {componentName} from ""
 */