import React, { Component } from 'react'
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  Label,
  Row,
  Col
} from 'reactstrap'
import { Control, LocalForm, Errors } from 'react-redux-form'

const maxLength = len => val => !val || val.length <= len
const minLength = len => val => val && val.length >= len

class CommentForm extends Component {
  constructor (props) {
    super(props)
    this.state = {
      isModalOpen: false
    }

    this.handleSubmit = this.handleSubmit.bind(this)
    this.toggleModal = this.toggleModal.bind(this)
  }

  handleSubmit (values) {
    this.toggleModal();  // To close the modal on click ofok button of the alert
    // // console.log('Current State is: ' + JSON.stringify(values))
    // // alert('Current State is: ' + JSON.stringify(values))
    this.props.postComment(this.props.dishId,values.rating,values.author,values.comment) 
    // onclick of submit button the addComment action is called, then it invokes the comment reducer with 
    // related case type and returns the associated new state with newly added comment 
  }

  toggleModal () {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    })
  }

  render () {
    return (
      <div className='container'>
        <div className='row'>
          <Button outline onClick={this.toggleModal}>
            <span className='fa fa-pencil'></span> Submit Comment
          </Button>
        </div>
        <div className='row'>
          <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
            <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
            <ModalBody>
              <LocalForm onSubmit={values => this.handleSubmit(values)}>
                <Row className='form-group'>
                  <Label htmlFor='rating' md={12}>
                    Rating
                  </Label>
                  <Col md={12}>
                    <Control.select
                      model='.rating'
                      id='rating'
                      name='rating'
                      className='form-control'
                    >
                      <option>1</option>
                      <option>2</option>
                      <option>3</option>
                      <option>4</option>
                      <option>5</option>
                    </Control.select>
                  </Col>
                </Row>
                <Row className='form-group'>
                  <Label htmlFor='author' md={12}>
                    Your Name
                  </Label>
                  <Col md={12}>
                    <Control.text
                      model='.author'
                      id='author'
                      name='author'
                      className='form-control'
                      placeholder='Author'
                      validators={{
                        minLength: minLength(3),
                        maxLength: maxLength(15)
                      }}
                    />
                    <Errors
                      className='text-danger'
                      model='.author'
                      show='touched'
                      messages={{
                        minLength: 'Must be greater than 2 characters',
                        maxLength: 'Must be 15 characters or less'
                      }}
                    />
                  </Col>
                </Row>
                <Row className='form-group'>
                  <Label htmlFor='comment' md={12}>
                    Comment
                  </Label>
                  <Col md={12}>
                    <Control.textarea
                      model='.comment'
                      id='comment'
                      name='comment'
                      className='form-control'
                      rows='6'
                    ></Control.textarea>
                  </Col>
                </Row>
                <Row className='form-group'>
                  <Col>
                    <Button type='submit' color='primary'>
                      Submit
                    </Button>
                  </Col>
                </Row>
              </LocalForm>
            </ModalBody>
          </Modal>
        </div>
      </div>
    )
  }
}

export default CommentForm
